<template>
  <div class="geo-map">
    <h2>üìç Âú∞ÁêÜÁºñÁ†Å + È´òÂæ∑Âú∞ÂõæÂ±ïÁ§∫</h2>

    <div class="search-bar">
      <input v-model="address" placeholder="ËØ∑ËæìÂÖ•Âú∞ÂùÄÔºà‰æãÂ¶ÇÔºö‰∏äÊµ∑Â∏ÇÂæêÊ±áÂå∫Ôºâ" />
      <button @click="search" :disabled="loading">
        {{ loading ? "Êü•ËØ¢‰∏≠..." : "Êü•ËØ¢" }}
      </button>
    </div>
    <!-- Êñ∞Â¢ûÁöÑÂùêÊ†áÊòæÁ§∫Ê°Ü -->
    <div class="coordinate-display">
      <label>ÂùêÊ†áÔºö</label>
      <input type="text" v-model="coordinates" readonly placeholder="Êü•ËØ¢ÂêéÊòæÁ§∫ÁªèÁ∫¨Â∫¶" />
    </div>

    <div id="mapContainer" class="map"></div>
  </div>
</template>

<script setup lang="ts">
/* eslint-disable */
import { ref, onMounted } from "vue";
import AMapLoader from "@amap/amap-jsapi-loader";

declare const AMap: any;

const address = ref("‰∏äÊµ∑Â∏Ç");
const loading = ref(false);

let map: AMap.Map | null = null;
let marker: AMap.Marker | null = null;

const key = "55323284f877424135064979b3c3718b"; // È´òÂæ∑ Key

onMounted(async () => {
  await AMapLoader.load({
    key,
    version: "2.0",
    plugins: ["AMap.Geocoder"],
  });

  map = new AMap.Map("mapContainer", {
    viewMode: "3D",
    zoom: 10,
    center: [121.4737, 31.2304],
  });

  marker = new AMap.Marker({
    map,
    position: [121.4737, 31.2304],
    title: "ÈªòËÆ§‰ΩçÁΩÆ",
  });
});

const search = async () => {
  if (!address.value.trim()) {
    alert("ËØ∑ËæìÂÖ•ÊúâÊïàÂú∞ÂùÄÔΩû");
    return;
  }

  loading.value = true;
  console.log("address:", address.value);
  try {
    const res = await fetch(
      `https://restapi.amap.com/v3/geocode/geo?key=${key}&address=${encodeURIComponent(
        address.value
      )}`
    );

    const data = await res.json();
    console.log("data:", data);
    if (data.status === "1" && data.geocodes.length > 0) {
      const [lng, lat] = data.geocodes[0].location.split(",");
      const location = new AMap.LngLat(lng, lat);
      map?.setCenter(location);
      marker.setPosition(location);
      map.add(marker);
      map.setFitView(marker);
    } else {
      alert("Âú∞ÂùÄËß£ÊûêÂ§±Ë¥•ÔºåËØ∑ËæìÂÖ•Êõ¥ËØ¶ÁªÜÂú∞ÂùÄÔºàÂ¶ÇÁúÅÂ∏ÇÂå∫Ë°óÈÅìÔºâ");
    }
  } catch (err) {
    console.error("Âú∞ÁêÜÁºñÁ†ÅÂ§±Ë¥•:", err);
    alert("Âú∞ÁêÜÁºñÁ†ÅËØ∑Ê±ÇÂ§±Ë¥•ÔºåËØ∑Ê£ÄÊü•ÁΩëÁªúÊàñAPI Key");
  } finally {
    loading.value = false;
  }
};
</script>

<style scoped>
.geo-map {
  padding: 20px;
  max-width: 100%;
}

.search-bar {
  margin-bottom: 10px;
}

input {
  padding: 8px;
  width: 250px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

button {
  padding: 8px 14px;
  background-color: #007acc;
  color: #fff;
  border: none;
  border-radius: 4px;
  margin-left: 10px;
  cursor: pointer;
  transition: all 0.2s;
}

button:disabled {
  background-color: #999;
  cursor: not-allowed;
}

.map {
  width: 100%;
  height: 790px;
  border-radius: 8px;
  overflow: hidden;
  margin-top: 10px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}
</style>
